import api from "./API";

const getProfile = () => {
console.log("Service getProfile");
  return api.get("/user/getProfile").then(
    response => {
      return response.data;
    },
    error => {
      return error;
    }
  );
};

const updateProfile = ({/*username, */email, password, firstName, lastName, address, fiscalCode}) => {
  return api.post("/user/updateProfile", {
    //username,
    email,
    password,
    firstName,
    lastName,
    address,
    fiscalCode,
  }).then(
    response => {
      return response.data;
    },
    error => {
      return error;
    }
  );
};


const getPublicContent = () => {
  return api.get("/test/all");
};

const getUserBoard = () => {
  return api.get("/test/user");
};

const getModeratorBoard = () => {
  return api.get("/test/mod");
};

const getAdminBoard = () => {
  return api.get("/test/admin");
};

const getAdminPanel = () => {
  return api.get("/admin/getAdminPanel"
  ).then(
    (response) => {
      return response.data;
    },
    (error) => {
      return error;
    }
  );
};

const UserService = {
  getProfile,
  updateProfile,
  getPublicContent,
  getUserBoard,
  getModeratorBoard,
  getAdminBoard,
  getAdminPanel,
};

export default UserService;
